@import url(https://fonts.googleapis.com/css2?family=Noto+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.about-us {
  background-image: url(/static/media/bg.09dfd23a.jpg);
  background-attachment: fixed;
  background-position: bottom;
  background-size: cover;
}

.about-us-header {
  width: 100%;
  text-align: center;
  padding: 60px 10%;
  margin: 0 auto;
  background-color: rgba(0, 0, 0, 0.3);
  -webkit-filter: blur(0.2);
          filter: blur(0.2);
  color: #fe7f6c;
}

.about-us-header h4 {
  font-size: 1.5rem;
  font-weight: 700;
  color: #fe7f6c;
}

.about-us-header h2 {
  letter-spacing: 2px;
  font-size: 2rem;
  text-transform: uppercase;
  color: white;
}

.about-us-header p {
  font-size: 1.4rem;
  font-weight: 600;
  letter-spacing: 1px;
  color: white;
}

@media (max-width: 600px) {
  .about-us-header {
    background-color: rgba(0, 0, 0, 0.1);
  }
  .about-us-header h2 {
    font-size: 1.8rem;
  }
  .about-us-header h4 {
    font-size: 1rem;
  }
  .about-us-header p {
    font-size: 0.9rem;
  }
  .about-us-header button {
    padding: 10px;
    font-size: 1rem;
  }
}

.about-us-middle-img {
  background-image: url(/static/media/aboutUsTopImage.20e6cab8.jpg);
  width: 100%;
  height: 300px;
  background-position: center;
  background-size: cover;
}

.about-us-third-content {
  display: flex;
  padding: 50px  10%;
  margin: 0 auto;
  background: #f2f2f2;
  justify-content: space-between;
  align-items: center;
}

.about-us-third-content .content {
  width: 50%;
  color: white;
}

.about-us-third-content .content h3 {
  margin: 20px 0;
  color: white;
}

.about-us-third-content img {
  width: 50%;
  height: auto;
}

@media (max-width: 600px) {
  .about-us-third-content {
    flex-direction: column;
    width: 100%;
  }
  .about-us-third-content .content {
    width: 100%;
  }
}

.our-team-carousel {
  padding: 40px;
  background-color: #927397;
  text-align: center;
}

.our-team-carousel .slick-slide img {
  margin: auto;
  width: 400px;
  height: 100%;
}

.about-us-our-clients {
  background-color: #ffe0e7;
  background: linear-gradient(326deg, #ffe0e7, #ecfafc);
  margin: auto;
  padding: 40px 0;
  text-align: center;
}

.about-us-our-clients .carousel .slick-slide img {
  margin: auto;
  width: 500px;
  padding: 20px;
}

@media (max-width: 600px) {
  .about-us-our-clients .carousel {
    padding: 10px;
    width: 100%;
  }
}

.about-us-contact-us {
  width: 80%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 50px 0;
}

.about-us-contact-us form {
  display: flex;
  width: 70%;
  flex-direction: column;
  margin-top: 35px;
}

.about-us-contact-us form .flex-input {
  display: flex;
  width: 100%;
  justify-content: space-between;
}

.about-us-contact-us form label {
  font-size: 1.1rem;
  font-weight: 400;
}

.about-us-contact-us form input {
  height: 30px;
  border-radius: 5px;
  padding: 10px;
  border: 1px solid;
  max-width: 100%;
  background-color: #ecfafc;
  color: #232323;
}

.about-us-contact-us form textarea {
  height: 150px;
  width: 100%;
  border-radius: 5px;
  padding: 10px;
  border: 1px solid;
  background-color: #ecfafc;
  color: #232323;
}

.about-us-contact-us form button {
  border: 2px solid black;
  border-radius: 3px;
  padding: 15px;
  transition: all 0.5s;
  text-transform: uppercase;
  margin: 10px 0;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 20px auto;
}

.about-us-contact-us form button:hover {
  background: black;
  color: white;
}

@media (max-width: 600px) {
  .about-us-contact-us {
    width: 100%;
  }
  .about-us-contact-us form {
    width: 90%;
  }
  .about-us-contact-us .flex-input {
    flex-direction: column;
    justify-items: center;
    width: 100%;
  }
  .about-us-contact-us .flex-input input {
    width: 100%;
    padding: 10px 0;
  }
}
/*# sourceMappingURL=AboutUs.css.map */
* {
  color: #232323;
  scroll-behavior: smooth;
  font-family: 'Noto Sans', sans-serif;
}

button {
  background: none;
  border: none;
}
/*# sourceMappingURL=App.css.map */
.nav-bar {
  background-color: #ecfafc;
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
  margin: 0;
  padding: 20px;
  z-index: 150;
}

.nav-bar .logo {
  display: flex;
  align-items: center;
}

.nav-bar .logo img {
  width: 30px;
  height: auto;
}

.nav-bar .logo h1 {
  font-size: 1.5rem;
  margin: 0 10px;
  color: #fe7f6c;
}

@media (max-width: 600px) {
  .nav-bar .logo img {
    width: 20px;
  }
  .nav-bar .logo h1 {
    font-size: 1.2rem;
  }
}
/*# sourceMappingURL=NavBar.css.map */
.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.footer .logo {
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer .logo img {
  width: 30px;
}

.footer .logo p {
  font-weight: bold;
  font-size: 1rem;
  color: #fe7f6c;
}

.footer .contacts ul {
  padding: 0;
  display: flex;
  list-style: none;
}

.footer .contacts ul li a {
  display: flex;
  align-items: center;
  margin: 10px;
  transition: all 0.2s;
}

.footer .contacts ul li a svg {
  margin: 0 5px;
  transition: all 0.2s;
}

.footer .contacts ul li a:hover {
  color: #fe7f6c;
  cursor: pointer;
}

.footer .contacts ul li a:hover svg path {
  color: #fe7f6c;
}

@media (max-width: 600px) {
  .footer .contacts ul {
    flex-direction: column;
  }
  .footer .contacts ul li {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
  }
  .footer .contacts ul li a {
    text-align: center;
  }
}
/*# sourceMappingURL=Footer.css.map */
